import React, { memo } from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input-count';
const CSS_STATE = 'vds-state';

const InputCount = memo(({
  ariaAtomic,
  ariaLive,
  children,
  className,
  show,
  ...remainingProps
}) => (
  <div
    aria-atomic={ariaAtomic}
    aria-live={ariaLive}
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--hidden`]: !show
    }, className)}
    {...remainingProps}
  >
    {children}
  </div>
));

InputCount.propTypes = {
  /**
  * Aria atomic
  */
  ariaAtomic: string,

  /**
  * Aria Live
  */
  ariaLive: string,

  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Show
   */
  show: bool
};

InputCount.defaultProps = {
  ariaAtomic: 'true',
  ariaLive: 'polite'
};

InputCount.displayName = 'InputCount';

export default InputCount;
