import React from 'react';
import { bool, node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-input-message';
const CSS_STATE = 'vds-state';

const InputMessage = ({
  children,
  className,
  invalid,
  show,
  ...remainingProps
}) => (
  <span
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--hidden`]: !show,
      [`${CSS_STATE}--error`]: Boolean(invalid)
    }, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

InputMessage.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Invalid
   */
  invalid: bool,

  /**
   * Show
   */
  show: bool
};

InputMessage.defaultProps = {
};

InputMessage.displayName = 'InputMessage';

export default InputMessage;
