import React, { forwardRef, Fragment, useCallback, useEffect, useRef, useState } from 'react';
import { bool, func, node, number, object, oneOfType, string } from 'prop-types';
import classnames from 'classnames';
import { debounce } from 'lodash';
import shortid from 'shortid';
import Icon from '../icon';
import InputBar from '../input-bar';
import InputContainer from '../input-container';
import InputCount from '../input-count';
import InputInput from '../input-input';
import InputLabel from '../input-label';
import InputMessage from '../input-message';
import InputTextarea from '../input-textarea';
import { getTextareaHeight } from './utils';

const Textarea = forwardRef(({
  ariaLabel,
  autoResize,
  className,
  containerProps,
  counterPrefix,
  css,
  disabled,
  errorIcon,
  errorText,
  helpIcon,
  helpText,
  id = shortid.generate(),
  invalid,
  label,
  maxCharacters,
  noValidate,
  onBlur,
  onChange,
  onFocus,
  readOnly,
  rows,
  showCounter,
  showErrorText,
  showHelpText,
  ...remainingProps
}, ref) => {
  ref = ref || useRef();
  const [remainingChars, setRemainingChars] = useState(maxCharacters);
  const [dirty, setDirty] = useState(false);
  const [filled, setFilled] = useState(false);
  const [focused, setFocused] = useState(false);
  const [isInvalid, setIsInvalid] = useState(invalid);
  const uniqueId = useRef(id);
  const uid = uniqueId.current;

  useEffect(() => {
    const textarea = ref.current;

    if (!noValidate) {
      if (dirty) {
        setIsInvalid((!textarea.checkValidity() || (textarea.required && !textarea.value.trim().length)));
      }

      if (!dirty && (textarea.required && !textarea.value.trim().length)) {
        setIsInvalid(true);
      }

      if (dirty && !textarea.required && !textarea.value.trim().length) {
        setIsInvalid(undefined);
      }
    }
  }, [dirty, focused, noValidate, ref]);

  useEffect(() => {
    const textarea = ref.current;

    if (textarea && autoResize) {
      textarea.style.height = '0';
      textarea.style.height = `${getTextareaHeight(rows, textarea)}px`;
    }
  }, [autoResize, ref, rows]);

  useEffect(() => {
    const textarea = ref.current;
    setRemainingChars(textarea.value ? (maxCharacters - textarea.value.length) : maxCharacters);
    setFilled(Boolean(textarea.value));
  }, [maxCharacters, ref]);

  useEffect(() => {
    setIsInvalid(invalid);
  }, [invalid]);

  const handleCountChange = useCallback(debounce(value => setRemainingChars(value ? (maxCharacters - value.length) : maxCharacters), 400), []);

  const handleBlur = e => {
    setFocused(false);

    if (typeof onBlur === 'function') {
      onBlur(e);
    }
  };

  const handleFocus = e => {
    setFocused(true);

    if (typeof onFocus === 'function') {
      onFocus(e);
    }
  };

  const handleChange = e => {
    const value = e.target.value;
    handleCountChange(value);
    setDirty(true);
    setFilled(value !== '');

    if (typeof onChange === 'function') {
      onChange(e);
    }
  };

  return (
    <InputContainer
      className={classnames(className)}
      css={css}
      disabled={disabled}
      filled={filled}
      focus={focused}
      readOnly={readOnly}
      isTextarea={!autoResize}
      invalid={isInvalid}
      {...containerProps}
    >
      {label ? <InputLabel htmlFor={uid}>{label}</InputLabel> : undefined}
      <InputBar>
        <InputInput>
          <InputTextarea
            aria-describedby={`input-message-help-${uid} input-message-error-${uid}`}
            aria-label={label ? undefined : ariaLabel}
            aria-invalid={isInvalid}
            id={uid}
            disabled={disabled}
            onBlur={handleBlur}
            onChange={handleChange}
            onFocus={handleFocus}
            readOnly={readOnly}
            ref={ref}
            {...remainingProps}
          />
        </InputInput>
      </InputBar>
      <span
        id={`input-message-${uid}`}
      >
        <InputMessage
          id={`input-message-help-${uid}`}
          invalid={isInvalid}
          show
        >
          {
            showHelpText && Boolean(helpText) && focused
              ? <Fragment>{helpIcon} {helpText}</Fragment>
              : ''
          }
        </InputMessage>
        <InputMessage
          id={`input-message-error-${uid}`}
          invalid={isInvalid}
          show
        >
          {
            isInvalid && showErrorText && Boolean(errorText)
              ? <Fragment>{errorIcon} {errorText}</Fragment>
              : ''
          }
        </InputMessage>
        <InputCount
          id={`count-${uid}`}
          show={showCounter && Boolean(maxCharacters)}
        >
          {
            showCounter && maxCharacters
              ? `${counterPrefix} ${remainingChars}`
              : ''
          }
        </InputCount>
      </span>
    </InputContainer>
  );
});

Textarea.propTypes = {
  /**
   * Aria Label
   */
  ariaLabel: string,

  /**
   * Auto resize
   */
  autoResize: bool,

  /**
   * @ignore
   */
  className: string,

  /**
   * Container Props
   */
  containerProps: object, // eslint-disable-line

  /**
   *  Counter Prefix
   */
  counterPrefix: string,

  /**
   * @ignore
   */
  css: func,

  /**
   * Disabled
   */
  disabled: bool,

  /**
   *  Error Message Icon
   */
  errorIcon: node,

  /**
   *  Error Message
   */
  errorText: node,

  /**
   * Input Help Message Icon
   */
  helpIcon: node,

  /**
   * Input Help Message
   */
  helpText: node,

  /**
   * ID
   */
  id: oneOfType([number, string]),

  /**
   * If true, component will go into invalid state.
   */
  invalid: bool,

  /**
   * Label
   */
  label: node,

  /**
   * The maximum number of characters that the user can enter.
   * If this value isn't specified, the user can enter an unlimited number of characters.
   */
  maxCharacters: oneOfType([number, string]),

  /**
   * Disable native HTML5 validation e.g. type=email, pattern="some regex pattern"
   */
  noValidate: bool,

  /**
   * Callback fired when the input element loses focus.
   */
  onBlur: func,

  /**
   * Callback fired when the input element value changes.
   */
  onChange: func,

  /**
   * Callback fired when the input element is focused on.
   */
  onFocus: func,

  /**
   * This Boolean attribute indicates that the user cannot modify the value of the control.
   * Unlike the disabled attribute, the readonly attribute does not prevent the user from
   * clicking or selecting in the control
   */
  readOnly: bool,

  /**
   * The number of visible text lines for the textarea.
   */
  rows: oneOfType([number, string]),

  /**
   * Show Counter
   */
  showCounter: bool,

  /**
   * Show Error Message
   */
  showErrorText: bool,

  /**
   * Show Help Message
   */
  showHelpText: bool
};

Textarea.defaultProps = {
  counterPrefix: 'Characters remaining',
  errorIcon: <Icon name="error" resolution="tiny" />
};

Textarea.displayName = 'Textarea';

export default Textarea;
