import React, { useCallback, useState } from 'react';
import Col from '../../col';
import Grid from '../../grid';
import Row from '../../row';
import Textarea from '../../textarea';
import Typography from '../../typography';

const TextareaExample = () => {
  const [state, setState] = useState({
    invalid: true,
    value: ''
  });

  const handleChange = useCallback(e => setState({
    invalid: e.target.value === '',
    value: e.target.value
  }), []);

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Textarea</Typography>
          <Typography tag="h2">Similar to input text with controlled and uncontrolled features</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Textarea
            label="Textarea Example"
            name="textarea-example"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Textarea prefilled</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Textarea
            label="Textarea Example"
            name="textarea-example"
            defaultValue="Some default value"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Textarea prefilled with Auto resize</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Textarea
            autoResize
            label="Textarea Example"
            name="textarea-example"
            defaultValue="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Textarea disabled</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Textarea
            label="Textarea Example"
            name="textarea-example" disabled
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Textarea readonly</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Textarea
            readOnly
            label="Textarea Example"
            name="textarea-example"
            defaultValue="Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Textarea error</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Textarea
            errorText="Please correct error"
            helpText="Please Enter some text, example Hello"
            invalid={state.invalid}
            label="Textarea Example"
            name="textarea-example"
            noValidate
            onChange={handleChange}
            showErrorText
            showHelpText
            value={state.value}
          />
        </Col>
      </Row>
      <hr />
      <Row>
        <Col>
          <Typography tag="h2">Textarea with Auto resize</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Textarea
            autoResize
            label="Textarea Example"
            name="textarea-example"
            rows="3"
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Textarea with Auto resize and text counter</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <form autoComplete="off">
            <Textarea
              autoComplete="off"
              autoResize
              label="Textarea Example"
              name="textarea-example"
              maxCharacters="350"
              rows="3"
              showCounter
            />
          </form>
        </Col>
      </Row>
    </Grid>
  );
};

export default TextareaExample;
