const getTextareaHeight = (rows = null, el) => {
  const {
    borderBottomWidth,
    borderTopWidth,
    lineHeight,
    paddingBottom,
    paddingTop
  } = window.getComputedStyle(el);

  const rowHeight =
    rows === null
      ? 0
      : parseFloat(lineHeight) * parseInt(rows, 10) +
      parseFloat(borderBottomWidth) +
      parseFloat(borderTopWidth) +
      parseFloat(paddingBottom) +
      parseFloat(paddingTop);

  const scrollHeight = el.scrollHeight + parseFloat(borderBottomWidth) + parseFloat(borderTopWidth);

  return Math.max(rowHeight, scrollHeight);
};

export { getTextareaHeight };
